// This loader ensures image paths work correctly with GitHub Pages
module.exports = function customLoader({ src, width, quality }) {
  const isProd = process.env.NODE_ENV === 'production';
  const repositoryName = 'Octopussai';
  
  if (isProd) {
    // For production, use the GitHub Pages URL structure
    return `https://vineetcoder5.github.io/${repositoryName}${src}?w=${width}&q=${quality || 75}`;
  }
  
  // For development, use the local path
  return `${src}?w=${width}&q=${quality || 75}`;
};
